import React from 'react';
import PropTypes from 'prop-types';
import { useStore } from '../store';

function partial(props) {
    const { data, index, handleClick, selectedIndex, childRefs } = props;
    const {
        action,
        disp: { listMetadata },
    } = data;

    return (
        <div
            ref={childRefs}
            role="row"
            id={`${index}`}
            key={`result-people-${index}`}
            aria-selected={selectedIndex === index}
            className="autocomplete-concierge__search-result-item"
            onClick={(e) => handleClick(e, action)}
            onKeyDown={(e) => handleClick(e, data)}
            tabIndex={-1}
        >
            <h3 className="title">
                {listMetadata.peopleFirstName && listMetadata.peopleFirstName}{' '}
                {listMetadata.peopleLastName && listMetadata.peopleLastName}
            </h3>
            <div className="details" role="gridcell">
                {listMetadata.peopleDepartment && (
                    <div className="autocomplete-results__metadata">
                        <svg className="svg-icon">
                            <use href="#building" />
                        </svg>
                        {listMetadata.peopleDepartment
                            .join(',')
                            .replace(',', ', ')}
                    </div>
                )}

                {listMetadata.peoplePhone && (
                    <div className="autocomplete-results__metadata">
                        <svg className="svg-icon">
                            <use href="#phone" />
                        </svg>
                        <span className="sr-only">Phone number</span>
                        {listMetadata.peoplePhone.join(', ')}
                    </div>
                )}

                {listMetadata.peopleEmail && (
                    <div className="autocomplete-results__metadata">
                        <svg className="svg-icon">
                            <use href="#email" />
                        </svg>
                        <span className="sr-only">Email</span>
                        {listMetadata.peopleEmail}
                    </div>
                )}
            </div>
        </div>
    );
}

export default function People(props) {
    const { id, label, className, templateId, childRefs } = props;
    const { results, selectedIndex } = useStore();

    const handleClick = (e, action) => {
        e.preventDefault();
        document.location.href = action;
    };

    if (!results[templateId] || results[templateId].length === 0) {
        return null;
    }

    return (
        <div
            id={`result-row-${id}`}
            className={[`autocomplete-concierge__section`, className].join(' ')}
            role="rowgroup"
        >
            <h2 className="autocomplete-concierge__section-title">{label}</h2>
            {results[templateId] &&
                results[templateId].map((data, index) =>
                    partial({
                        data,
                        index,
                        handleClick,
                        selectedIndex,
                        childRefs,
                    })
                )}
        </div>
    );
}

const { string, number, func } = PropTypes;

People.propTypes = {
    id: string.isRequired,
    label: string,
    className: string,
    templateId: number.isRequired,
    childRefs: func.isRequired,
};

People.defaultProps = {
    label: ``,
    className: ``,
};
